        p.mb-4 Las excepciones se clasifican en dos categor√≠as principales: excepciones verificadas (<i>checked exceptions</i>) y excepciones no verificadas (<i>unchecked exceptions</i>).
        .bg-morado.p-4(data-aos='fade-up')
