    .row.mb-4
      .col-12.col-xl-8
        p.mb-4 Por otro lado, las excepciones no verificadas no requieren ser declaradas ni manejadas expl√≠citamente. Estas excepciones heredan de la clase RuntimeException o Error y son generalmente causadas por errores de programaci√≥n, como acceso a √≠ndices fuera de los l√≠mites de un arreglo (ArrayIndexOutOfBoundsException) o divisi√≥n por cero (ArithmeticException).
